.main_title {
  color: #eee;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 26px;
  font-weight: 600;
  margin: 10px 10px 20px;
  margin: 30px 0 0 20px ;
}

.main_form{
  margin: 0 auto;
  margin-top: 60px;
  height: 400px;
  width: 400px;
  background-color: #11994f;
  border-radius: 20px;
  box-sizing: border-box; 
  padding: 20px;
}

@media screen and (min-width:320px) and (max-width:640px) {
  .main_form{
    margin: 0 auto;
    margin-top: 80px;
    height: 400px;
    width: 85%;
    background-color: #11994f;
    border-radius: 10px;
    box-sizing: border-box; 
    padding: 20px;
  }
}
 


.main_submit{
  background-color: #ffffff;
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: #000;
  cursor: pointer;
  font-size: 18px;
  height: 50px;
  margin-top: 38px;
  outline: 0;
  text-align: center;
  width: 100%;
}

.main_submit:active {
  background-color: #ffffff;
}

.main_submit:hover{
  background-color: #e8f7eb;
}